:root {
  --header-background: url("./header-background.jpg");
  --primary-color: #5BD9FB;
  --background-500-color: #0C172B;
}

body {
  background: var(--background-500-color);
  font-size: 18px;
  color: #ffffff;
  font-family: 'Titillium Web', sans-serif;
}

p {
  line-height: 25px;
  opacity: 0.7;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 0 15px;
}

.header {
  height: 100vh;
  background-image: var(--header-background);
  background-size: cover;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  overflow: hidden;
}

.header__title {
  text-transform: uppercase;
  z-index: 1;
  font-size: 72px;
  white-space: nowrap;
  text-shadow: 1px 1px 10px #000000;
  color: #ffffff;
  text-transform: uppercase;
  margin: 0;
}

.header__title-secondary-wrapper {
  overflow: hidden;
  z-index: 1;
  margin-top: 10px;
}

.header__title-secondary {
  color: var(--primary-color);
  font-size: 48px;
  text-transform: uppercase;
  margin: 0;
}

.header__pieces {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  -webkit-perspective: 400px;
          perspective: 400px;
}

 .header__piece-wrap {
   position: absolute;
   width: 100%;
   height: 100%;
 }

.header__piece {
  background-image: var(--header-background);
  background-size: cover;
  width: 100%;
  height: 100%;
}

.header__piece__shadow {
  position: absolute;
  -webkit-box-shadow: 0 7px 30px 1px rgba(0, 0, 0, 0.45);
          box-shadow: 0 7px 30px 1px rgba(0, 0, 0, 0.45);
}

.next-section {
  height: 1000px;
  background: var(--background-500-color);
  text-align: center;
  padding: 100px 0;
}

.section-title {
  font-size: 32px;
  color: #ffffff;
}

.react-logo {
  margin: 30px auto 0;
  display: inline-block;
  width: 80px;
  height: 80px;
  position: relative;
  -webkit-animation: meat-spin 10s infinite linear;
          animation: meat-spin 10s infinite linear;
}

.react-logo__ball {
  width: 14px;
  height: 14px;
  background: var(--primary-color);
  border-radius: 50%;
  left: 50%;
  top: 50%;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.react-logo__oval {
  width: 80px;
  height: 30px;
  border-radius: 50%;
  background: var(--primary-color);
  opacity: 0.3;
  left: 50%;
  top: 50%;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.react-logo__oval--second {
  -webkit-transform: rotate(60deg);
      -ms-transform: rotate(60deg);
          transform: rotate(60deg);
  left: 0;
  top: 25px;
}

.react-logo__oval--third {
  -webkit-transform: rotate(-60deg);
      -ms-transform: rotate(-60deg);
          transform: rotate(-60deg);
  left: 0;
  top: 25px;
}

@-webkit-keyframes meat-spin {
  0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  100% {-webkit-transform: rotate(359deg);transform: rotate(359deg);}
}

@keyframes meat-spin {
  0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  100% {-webkit-transform: rotate(359deg);transform: rotate(359deg);}
}


